.RealidadeAumentada {
    justify-content: center;
    align-items: center;
}

.alignElements{
    display: flex;
    justify-content: center;
    align-items: center;
}

.alignImage{
    display: block;
    align-self: center;
    opacity: 0.7;
}

.alignedBtn{
    position: absolute;
    display: flex;
    background-color: #6FAFC9;
    border: none;
    color: white;
    padding: 30px;
    font-size: 30px;
    border-radius: 50%;
    z-index: 99;
}